* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: #f8f9fa;
  color: #333;
}

.container {
  overflow-y: hidden;
  height: 100vh;
}

/* MAIN NAVIGATION */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 0.2rem 0.8rem 0.1rem rgb(0, 0, 0, 0.2);
  padding: 0 2rem;

  position: sticky;

  top: 0;
  left: 0;
}

.main-nav {
  font-size: 2.2rem;
}

.main-nav-list {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  padding: 2.4rem 0;

  list-style: none;
}

.main-nav-link {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  letter-spacing: 0.05rem;
}

.page-content-container {
  display: flex;
  height: 100%;
}

.login-form-container {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

.input-username,
.input-password {
  max-width: 12rem;
  margin-right: 1.2rem;
}

.form-login,
.cta-signup {
  font-size: 1.4rem;
  font-weight: 600;
}

.label-username,
.label-password {
  margin-right: 0.5rem;
}

.cta-signup {
  color: #228be6;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
}

.cta-signup:hover {
  cursor: pointer;
  color: #64aeee;
}

/* ASIDE */

.main-aside {
  width: 35rem;
  background-color: #f4f5f7;
  height: 100%;

  padding: 2rem;
  border-right: 0.2rem solid rgb(51, 51, 51, 0.1);
}

.project-selection {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  margin-bottom: 2.4rem;
}

.project-icon {
  width: 6rem;
  height: 6rem;
  border-radius: 1rem;
  background-color: #228be6;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.2rem;
  stroke: #000;
}

.aside-project-title {
  font-size: 1.8rem;
  font-weight: 500;
}

.aside-project-type {
  color: #555;
  font-size: 1.4rem;
  font-weight: 400;
}

.aside-nav {
  font-size: 1.8rem;
}

.aside-nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.aside-nav-item {
  padding: 0.4rem;
  border-radius: 1rem;
}

.aside-nav-item:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.aside-nav-link {
  text-decoration: none;
  color: #444;
}

/* MAIN CONTENT PANEL */

.section-main {
  padding: 2.4rem;
  width: 100%;
}

.text-path {
  font-size: 1.6rem;
  font-weight: 400;
  color: #555;
}

.content-heading {
  font-size: 3rem;
  color: #228be6;
  margin-bottom: 3.2rem;
}

/* BUGS LIST HEADER */

.bugs-list-header {
  width: auto;
  border-bottom: 0.1rem solid rgb(51, 51, 51, 0.5);
  margin-bottom: 1.2rem;
}

.bugs-list {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr;
  align-items: center;
  /* justify-items: center; */
  list-style: none;
}

.bugs-header-list-item {
  font-size: 1.8rem;
  font-weight: 600;
}

/* USER BUG LIST */

.bugs-list--user {
  font-size: 1.6rem;
  margin-top: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
}

.status-icon {
  height: 2.4rem;
  width: 2.4rem;
}

.bugs-list--user:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

/*
SPACING SYSTEM (px)
 2 / 4 / 8 / 12 / 16 / 20 / 24 / 32 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98
*/

.tint-blue {
  color: #228be6;
}

.tint-red {
  color: red;
}

.tint-green {
  stroke: #51cf66;
  color: #51cf66;
}
